
import './App.css';
import React, {useState, useEffect} from 'react';
import axios from 'axios';
import Coin from './Coin';

function App() {

  const [coins,setCoins] = useState([])

const[search, setSearch] = useState('');



useEffect(() => {
axios.get('https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=250&page=1&sparkline=false').then
(res => {
setCoins(res.data);

}).catch(error => alert('Error'));
},[]);

const handleChange = e => {
  setSearch(e.target.value)
}

const filterCoins = coins.filter(coin =>
  coin.name.toLowerCase().includes(search.toLowerCase())
)

  return (
    <div className="coin-app">




     <div className = "coinquery">
     <h1 className="coin-info">Search a currency</h1>
     <form>
<input type ="text" placeholder="Search"
className="coin-input" onChange = {handleChange}/>

     </form>

<p class="info"> A react app using the CoinGecko API
</p>

    <img   class="imgs" src="logo192.png" alt="react" />


    </div>

<div className  = "catCont">
    <div className = "cat">
<p>Name</p>
<div className = "catdata">
<p>Price</p>
<p>24h%</p>
<p>Market Cap</p>
<p>Volume(24h)</p>
<p>Supply</p>
</div>
</div>
</div>
    

    {filterCoins.map(coin => {
      return <Coin key = {coin.id} name = {coin.name}
        image = {coin.image} 
        symbol = {coin.symbol}
        marketcap = {coin.market_cap}
        price = {coin.current_price}
        priceChange ={coin.price_change_percentage_24h}
        volume = {coin.total_volume}
        supply = {coin.circulating_supply}/>;
      
    })}


    </div>
  );
}

export default App;
